    import {
        mapState,
        mapMutations
    } from 'vuex'
    import mInput from '../../components/m-input.vue'

    export default {
        components: {
            mInput
        },
        data() {
            return {
               phone:"",
			   code:"",
			   passwordNew:"",
			   password:"",
			   codeSend:false,
			   time:60,
            }
        },
        methods: {
			checkPhone(phone){
				if(!(/^1[3456789]\d{9}$/.test(phone))){
					return false;
				}
				return true
			},
			checkboxChange(e){
				this.checkArr=e.detail.value;
			},
			sendCode(){
				var that=this
				
				if(this.codeSend){
					return;
				}
				if(!that.checkPhone(that.phone)){
					uni.showToast({
						title:"请填写正确的手机号码",
						icon:"none"
					})
				}
				this.codeSend=true;
				var t=setInterval(function(){
					console.log(that.time)
					that.time-=1;
					if(that.time==0){
						that.codeSend=false;
						that.time=60;
						clearInterval(t)
					}
				},1000)
				this.$ajax({
					url:"/sms",
					data:{
						phone:that.phone,
						type:"SMS_161591480"
					},
					success(d){
						
					}
				})
			},
            bindLogin() {
				let that=this;
				if(that.phone&&that.code&&that.password&&that.passwordNew){
					if(that.password!=that.passwordNew){
						uni.showToast({
							title:"两次输入的密码不一致",
							icon:"none"
						})
						return
					}
					that.$ajax({
						url:"/system/retrievePassword",
						data:{
							code:that.code,
							phone:that.phone,
							smsType:"SMS_161591480",
							newPassword:that.password,
							type:0
						},success(d){
							uni.showToast({
								title:"操作成功",
								icon:"none"
							})
							setTimeout(function(){
								uni.reLaunch({
									url: '../login/login'
								});
							},1500)
						}
					})
				}else{
					uni.showToast({
						title:"请输入完整的信息",
						icon:"none"
					})
				}
            },

		},
        onReady() {

        }
    }
